import React from 'react'
import styled from 'styled-components'

const Li = styled.li`
  padding: 5px 20px;
  margin: 4px 1px;
  border: none;
  color: white;
  width: 100px;
  text-align: center;
  border-radius: 25px;
  list-style: none;
  &:hover {
    transform: scale(1.1);
    box-shadow: 0 0 4px 3px rgba(0, 0, 0, 0.1);
  }
`

const Reason = ({ reason }) => {
  return <Li style={{ background: `${reason.color}` }}>{reason.name}</Li>
}

export default Reason
